import Vue from 'vue';
import i18n from '../../../i18n';
import { faListUl, faICursor, faUsers, faPlus } from '@fortawesome/free-solid-svg-icons';
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons';
export default Vue.extend({
    i18n: i18n('common/views/components/user-list-editor.vue'),
    props: {
        listId: {
            required: true
        }
    },
    data() {
        return {
            list: null,
            users: [],
            faListUl, faICursor, faTrashAlt, faUsers, faPlus
        };
    },
    created() {
        this.$root.api('users/lists/show', {
            listId: this.listId
        }).then(list => {
            this.list = list;
            this.fetchUsers();
            this.$emit('init', {
                title: this.list.name,
                icon: faListUl
            });
        });
    },
    methods: {
        fetchUsers() {
            this.$root.api('users/show', {
                userIds: this.list.userIds
            }).then(users => {
                this.users = users;
            });
        },
        rename() {
            this.$root.dialog({
                title: this.$t('rename'),
                input: {
                    default: this.list.name
                }
            }).then(({ canceled, result: name }) => {
                if (canceled)
                    return;
                this.$root.api('users/lists/update', {
                    listId: this.list.id,
                    name: name
                });
            });
        },
        del() {
            this.$root.dialog({
                type: 'warning',
                text: this.$t('delete-are-you-sure').replace('$1', this.list.name),
                showCancelButton: true
            }).then(({ canceled }) => {
                if (canceled)
                    return;
                this.$root.api('users/lists/delete', {
                    listId: this.list.id
                }).then(() => {
                    this.$root.dialog({
                        type: 'success',
                        text: this.$t('deleted')
                    });
                }).catch(e => {
                    this.$root.dialog({
                        type: 'error',
                        text: e
                    });
                });
            });
        },
        remove(user) {
            this.$root.api('users/lists/pull', {
                listId: this.list.id,
                userId: user.id
            }).then(() => {
                this.fetchUsers();
            });
        },
        async add() {
            const { result: user } = await this.$root.dialog({
                user: {
                    local: true
                }
            });
            if (user == null)
                return;
            this.$root.api('users/lists/push', {
                listId: this.list.id,
                userId: user.id
            }).then(() => {
                this.fetchUsers();
            });
        }
    }
});
